.center-div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .left-side, .right-side {
    width: 45%;
    height: 80vh;
    padding: 20px;
    border: 1px solid #ddd;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    margin: 10px;
  }
  
  .left-side {
    resize: none;
    outline: none;
    font-size: 16px;
    font-family: Arial, sans-serif;
  }
  
  .right-side {
    overflow-y: auto;
    font-size: 16px;
    font-family: Arial, sans-serif;
    background-color: #f9f9f9;
  }
  